@import url(https://fonts.googleapis.com/css?family=Muli:400,700);
.week-number {
  position: absolute;
  top: 0;
  left: 0;
}

.week-number--current {
  width: 3rem;
  height: 3rem;
  background-color: #ff0;
}

.week-number--nav--year {
  padding: 0 1rem;
  line-height: 1.5rem;
  background: #191414;
}

.week-number--nav-container {
  display: none;
  color: #fff;
  position: absolute;
  width: 400px;
  top: 0;
  left: 3rem;
  background: #191414;
}

@media (max-width: 768px) {
  .week-number--nav-container {
    width: calc(100vw - 3rem);
  }
}

.week-number--current:hover + .week-number--nav-container,
.week-number--nav-container:hover {
  display: block;
}

.week-number--list {
  max-height: 0px;
  overflow: hidden;
}

.week-number--current:hover + .week-number--nav-container .week-number--list,
.week-number--nav-container:hover .week-number--list {
  max-height: 400px;
  overflow: auto;
}

.week-number--list-item {
  display: block;
  line-height: 1.5rem;
  background: #fff;
  color: #191414;
  -webkit-transition: all 0.1s ease;
  transition: all 0.1s ease;
  text-decoration: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  outline: 0;
}

.week-number--list-item:hover {
  background: #191414;
  color: #fff;
}

.week-number--list-item:active:hover {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.week-number--list-item .week-number--list-item--week {
  display: inline-block;
  padding: 0.5rem;
  margin-right: 0.5rem;
  background: #ff0;
  color: #191414;
  -webkit-transition: all 0.1s ease;
  transition: all 0.1s ease;
}

.week-number--list-item:hover .week-number--list-item--week {
  background: #191414;
  color: #ff0;
}

.week-number--list-item .week-number--list-item--week > span {
  display: block;
  -webkit-transition: -webkit-transform 0.1s ease;
  transition: -webkit-transform 0.1s ease;
  transition: transform 0.1s ease;
  transition: transform 0.1s ease, -webkit-transform 0.1s ease;
}

.week-number--list-item:hover .week-number--list-item--week > span {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.week-number .week-number--navigation,
.week-number .week-number--current {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.week-number--navigation {
  background-color: #191414;
  height: 1.5rem;
  color: #fff;
  text-decoration: none;
  -webkit-transition: all 0.1s ease;
  transition: all 0.1s ease;
}

.week-number--navigation:hover {
  background-color: #fff;
  color: #191414;
}

.week-number--navigation > span {
  -webkit-transition: -webkit-transform 0.1s ease;
  transition: -webkit-transform 0.1s ease;
  transition: transform 0.1s ease;
  transition: transform 0.1s ease, -webkit-transform 0.1s ease;
}

.week-number--navigation.left:active > span {
  -webkit-transform: translateX(0.5rem);
          transform: translateX(0.5rem);
}

.week-number--navigation.right:active > span {
  -webkit-transform: translateX(-0.5rem);
          transform: translateX(-0.5rem);
}

.filter-input {
  display: block;
  margin: 0;
  width: 100%;
  border: none;
  line-height: 3rem;
  padding: 0 0.5rem;
  box-sizing: border-box;
  background: #000;
  color: #fff;
}

.secondary-label {
  font-style: italic;
  margin-left: 0.5rem;
  opacity: 0.7;
}

.backdrop {
  background-size: cover;
  background-position: 50%;
  /* Add the blur effect */
  filter: blur(0.75);
  -webkit-filter: blur(0.75rem);
  position: fixed;
  top: -2rem;
  left: -2rem;
  bottom: -2rem;
  right: -2rem;
  z-index: -1;
}

.opacity-anim-enter {
  opacity: 0.01;
}

.opacity-anim-enter.opacity-anim-enter-active {
  opacity: 1;
  -webkit-transition: all 100ms ease;
  transition: all 100ms ease;
}

.opacity-anim-leave {
  opacity: 1;
}

.opacity-anim-leave.opacity-anim-leave-active {
  opacity: 0.01;
  -webkit-transition: all 100ms ease;
  transition: all 100ms ease;
}

.album-info {
  margin: auto auto 0;
  max-width: 800px;
}

.album-info--cover img {
  max-width: 19rem;
  width: 100%;
}

.album-info--meta {
  padding-left: 2rem;
  color: #fff;
}

.album-info--meta .heading {
  font-size: 3rem;
  margin: 0 0 1rem;
}

.album-info--meta .subheading {
  font-size: 2rem;
  margin: 0 0 1rem 0;
}

.album-info--meta .heading a,
.album-info--meta .subheading a {
  text-decoration: none;
  color: white;
}

.album-info--meta .heading a:hover,
.album-info--meta .subheading a:hover {
  text-decoration: underline;
}

.album-info--meta .subheading .year {
  font-style: italic;
  opacity: 0.5;
}

.album-info--anim-enter {
  -webkit-transform: scale(0.85);
          transform: scale(0.85);
  opacity: 0.01;
}

.album-info--anim-enter.album-info--anim-enter-active {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
  -webkit-transition: all 400ms ease;
  transition: all 400ms ease;
}

.album-info--anim-enter.album-info--anim-enter-active:nth-child(even) {
  -webkit-transition-delay: 0.15s;
          transition-delay: 0.15s;
}

.album-info--anim-leave {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}

.album-info--anim-leave.album-info--anim-leave-active {
  -webkit-transform: scale(0.85);
          transform: scale(0.85);
  opacity: 0.01;
  -webkit-transition: all 400ms ease;
  transition: all 400ms ease;
}

.album-info--anim-leave.album-info--anim-leave-active:nth-child(odd) {
  -webkit-transition-delay: 0.15s;
          transition-delay: 0.15s;
}

.tag {
  display: inline-block;
  margin-right: 0.5rem;
  color: #fff;
  text-decoration: none;
  opacity: 0.7;
  font-style: italic;
  font-weight: bold;
}

.tag:hover {
  opacity: 1;
}

.tag:last-child {
  margin-right: 0;
}

.tags {
  margin-bottom: 2rem;
}

.external-action {
  display: inline-block;
  padding: 1rem;
  background: #191414;
  border-radius: 0.1875rem;
  color: white;
  font-size: 1.125rem;
  text-decoration: none;
  -webkit-transition: all 0.1s ease;
  transition: all 0.1s ease;
  margin-right: 1rem;
}

.external-action:hover {
  color: #191414;
  background: white;
}

.external-action:active {
  -webkit-transform: scale(0.95);
          transform: scale(0.95);
}

.external-action svg {
  margin-right: 1rem;
  display: inline-block;
  vertical-align: middle;
}

@media (min-width: 769px) {
  .album-info,
  .album-info > span {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media (max-width: 768px) {
  .album-info {
    margin-top: 10rem;
    flex-direction: column;
    padding: 2rem;
  }

  .album-info--meta {
    margin-top: 1rem;
    padding: 0;
  }

  .album-info--meta .heading {
    font-size: 2rem;
  }

  .external-action {
    font-size: 1rem;
    margin-bottom: 1rem;
    padding: 0.75rem;
  }
}

.scrollable {
  align-self: center;
  min-height: 0;
  padding-bottom: 4rem;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
  gap: 1rem;
}

.grid > .grid-entry:nth-child(1) {
  grid-column: 1 / 3;
}

.grid > .grid-entry {
  display: flex;
  background: #191414;
  color: #fff;
  text-decoration: none;
}

.grid .grid-entry--weeknum {
  height: 4rem;
  width: 4rem;
  line-height: 4rem;
  text-align: center;
  background-color: #ff0;
  color: #000;
  margin-right: 1rem;
}

.grid-entry--info {
  flex: 1 1;
  margin-right: 1rem;
}

.grid > .grid-entry:nth-child(1) {
  grid-column: 1 / 3;
}

.grid > .grid-entry:nth-child(1) .grid-entry--weeknum {
  height: 6rem;
  width: 6rem;
  line-height: 6rem;
}

html,
body,
#root {
  height: 100%;
}

body {
  background: #333;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: 'Muli', sans-serif;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
  overflow: scroll;
}

.byline {
  opacity: 0.75;
  align-self: end;
  justify-self: flex-end;
  margin-top: auto;
  color: white;
  font-size: 1rem;
  padding: 0.5rem;
}

.byline a {
  color: yellow;
  text-decoration: none;
}

.byline a:hover {
  color: white;
}

@media (max-width: 768px) {
  body {
    font-size: 14px;
  }
}

@media (max-height: 500px) {
  .wrapper {
    position: static;
  }
}

